	import divider from '@/components/divider.vue';
	import freeAvater from'@/components/free-avater.vue'
	import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'
	
	//引入网络请求
	import $H from '@/common/request.js';
	import $C from '@/common/config.js';
	import $U from '@/common/util.js';
	
	
	export default {
		components:{
			divider,
			freeAvater,
			uniLoadMore
		},
		data() {
			return {
				background:{
					backgroundColor: '#fff',
				},
				fans:0,
				isMoren:true,
				order:1,
				orderSrc:'../../../static/common/paixu1.png',
				page:1,
				listData: [],
				reload: true,
				loadMore: false,
				status: 'more',
				keyword:"",
				contentText: {
					contentdown: '查看更多',
					contentrefresh: '加载中',
					contentnomore: '没有更多'
				}
			}
		},
		
		onLoad() {
			// 获取粉丝数量
			this.getCount();
			
			//获取粉丝列表
			this.getData();
		},
		
		onPullDownRefresh() {
			this.reload = true;
			this.loadMore = false;
			this.page=1;
			this.status = 'more';
			
			if(this.keyword!=""){
				this.reload = true;
				if(this.keyword.startsWith("1")){
					this.getData("",this.keyword)
				}
				else{
					this.getData(this.keyword,"")
				}
			}
			else{
				this.getData();
				this.getCount();
			}
			
		},
		
		onReachBottom() {
			this.loadMore = true;
			this.page++;
			if (this.status == 'noMore') {
				return;
			}
			this.status = 'loading';
			if(this.keyword!=""){
				this.reload = true;
				if(this.keyword.startsWith("1")){
					this.getData("",keyword)
				}
				else{
					this.getData(keyword,"")
				}
			}
			else{
				this.getData();
			}
			
		
		},
		
		methods: {
			
			search(keyword){
				this.page=1;
				this.reload = true;
				if(keyword.startsWith("1")){
					this.getData("",keyword)
				}
				else{
					this.getData(keyword,"")
				}
			},
			
			
			// 获取粉丝数量
			getCount(){
				// 发送到服务端
				$H.get("/customer_fans/count").then((res) => {
					//请求成功
					this.fans=res.data+"人"
					
				}).catch((e) => {
					
					//请求失败
					console.log("失败"+e)
				})
			},
			
			// 获取粉丝列表
			getData(name,phone){
				
				uni.showLoading({
					title: '加载中'
				});
				
				let param={
					size:10,
					current:this.page,
					wx_nick_name:name||"",
					mobile_number:phone||""
				}
				
			
				// 发送到服务端
				$H.get("/customer_fans/page/all",param).then((res) => {
					uni.stopPullDownRefresh()
					uni.hideLoading();
					
					let list = res.data.records;
                    let pages=res.data.pages;
                     
					if(pages>1&&this.page<=pages){
						this.listData = this.reload ? list : this.listData.concat(list);
					}
					else if(pages==0){
						this.listData=[];
						this.status ='noMore'
					}
					else if(pages==1){
						this.listData = list
						this.status ='noMore'
					}
					else{
						this.status ='noMore'
					}
					
					console.log("失败"+this.listData)
					
					this.reload = false;
					
					
				}).catch((e) => {
					
					uni.hideLoading();
					uni.stopPullDownRefresh();
					//请求失败
					console.log("失败"+e)
				})
			},
			
			//默认排序
			moren(){
				this.isMoren=true
				this.order=1
				this.orderSrc='../../../static/common/paixu1.png'
			},
			
			paixu(index){
				this.isMoren=false
				if(index==2||index==1){
					this.order=3
					this.orderSrc='../../../static/common/paixu2.png'
				}
				
				 if(index==3){
					this.order=2
					this.orderSrc='../../../static/common/paixu3.png'
				}
				console.log(this.order)
			}
		}
	}
